import React, { useEffect, useRef } from 'react';
import * as THREE from 'three';
import '../css/skeleton.css';
import '../css/normalize.css';
import '../css/components.css';
import profile from '../assets/profile.jpg';
import logo from '../assets/logo.gltf';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader';

function Header() {
  const mountRef = useRef(null);
  const modelRef = useRef(null);
  const sceneRef = useRef(null);
  const rendererRef = useRef(null);
  const cameraRef = useRef(null);

  useEffect(() => {
    // Scene setup
    const scene = new THREE.Scene();
    sceneRef.current = scene;

    // Camera setup
    const camera = new THREE.PerspectiveCamera(75, 400 / 400, 0.1, 1000);
    camera.position.z = 5;
    cameraRef.current = camera;

    // Renderer setup
    const renderer = new THREE.WebGLRenderer({ antialias: true, alpha: true });
    renderer.setSize(450,450);
    renderer.domElement.style.display = 'block';
    renderer.domElement.style.margin = '0 auto';
    rendererRef.current = renderer;
    mountRef.current.appendChild(renderer.domElement);

    // Lighting
    const ambientLight = new THREE.AmbientLight(0xffffff, 0.5);
    scene.add(ambientLight);

    const directionalLight = new THREE.DirectionalLight(0xffffff, 0);
    directionalLight.position.set(5, 5, 5);
    scene.add(directionalLight);

    // Load GLTF model
    const loader = new GLTFLoader();
    loader.load(
      logo,
      
      function (gltf) {
        gltf.scene.scale.set(0.4, 0.4, 0.4); // Scale down to 50%
        modelRef.current = gltf.scene;
        scene.add(gltf.scene);
      },
      undefined,
      function (error) {
        console.error('Error loading GLTF model:', error);
      }
    );

    // Animation loop
    function animate() {
      requestAnimationFrame(animate);
      if (modelRef.current) {
        //modelRef.current.rotation.x += 0.01;
        modelRef.current.rotation.y += 0.01;
        //modelRef.current.rotation.z += 0.01;
      }
      renderer.render(scene, camera);
    }
    animate();

    // Cleanup
    
  }, []);

  return (
    <>
    <div className="Header container">
      <div className="ten columns">
        @thankyoudom
      </div>
      <div className="profile-header">
        <div className="profile-info">
          <h1>Dominique</h1>
          <p>Producer, Designer</p>
          <i className="fas fa-envelope" style={{ color: 'gray' }} aria-hidden="true"></i> 
                                    <a className="profile-info" href="mailto:info@thankyoudom.com">info@thankyoudom.com</a>
        </div>
        <div className="profile-image">
          <img src={profile} alt="Profile Picture"/>
        </div>
        
      </div>
      
    </div>
<div className = "container row">
    <div ref={mountRef} className="Card3"></div>
    </div>
    
    </>
  );
}

export default Header;